@tailwind base;
@tailwind components;
@tailwind utilities;

body.light-mode {
    background-color: #fff;
    color: #333;
    transition: background-color 0.3s ease;
  }
  body.dark-mode {
    background-color: #1a1919;
    color: #999;
  }
  
.navbar-fixed{
    @apply fixed bg-white opacity-70;
    backdrop-filter: blur(5px);
    box-shadow: inset 0 -1px 0 0 rgba(0,0,0,0.1);
    
}

body{
    font-family: 'Inter', sans-serif;
}

.hamburger-garis{
    @apply w-[25px] h-[1px] my-2 block bg-black;
}

.active > span:nth-child(1){
    @apply rotate-45;
}

.active > span:nth-child(3){
    @apply -rotate-45;
}

.active > span:nth-child(2){
    @apply scale-0;
}

.qualification_data{
    display: grid;
    grid-template-columns: 1fr max-content 1fr;
    column-gap: 1.5rem;
}

#toggle_darkmode:checked ~ label div.main-toggle{
    @apply transition  duration-500 ease-in-out bg-slate-600;
 }

#toggle_darkmode:checked ~ label div div.toggle-moon{
   @apply translate-x-8 rotate-[360deg];
}
    
